import { useState, useEffect } from "react";
import { db } from '../data/firebase.database';

const useFetch = () => {
    const [errors, setErrors] = useState([]);
    useEffect(() => {
        db.collection("errors").onSnapshot((errors) => 
        setErrors(
            errors.docs.map((errarDate) => ({
                uniqueId: errarDate.id,
                data: errarDate.data()
            })) 
        )
    )
    }, []);
    return [errors]
}

export default useFetch;