import React from 'react';
import classes from './Errors.module.css';

import ErrorItem from '../../components/error-item/ErrorItem';

import useFetch from '../../hooks/useFetch';


const Errors = () => {
    const [errors] = useFetch();
    console.log(errors)
    return (
        <div className={classes.errors}>
            <div className={classes.container}>
                <h1 className={classes.errors__title}>
                    All Errors for Today
                </h1>
                <div className={classes.errors__container}>
                    {
                        errors?.map((error, index, array) => (
                            <ErrorItem
                                key={index}
                                errorAuthor={error?.data.error.errorAuthor}
                                errorImage={error?.data.error.errorImage}
                                errorLikers={error?.data.error.errorLikers}
                                errorLikes={error?.data.error.errorLikes}
                                errorName={error?.data.error.errorName}
                                errorDescription={error?.data.error.errorDescription}
                            />
                        ))
                    }
                </div>
            </div>
        </div>
    )
}

export default Errors
